<template>
  <div class="wrapper">
    <div class="title">购物车</div>
    <template v-for="item in cartList" :key="item">
      <template v-if="Object.keys(item.productList).length > 0">
        <ProductList :shopName="item.shopName" :productList="item.productList" />
      </template>
    </template>
    <div v-if="isCartEmpty" class="tips">
      购物车无商品
    </div>
  </div>
  <Docker :currentIndex="1" />
</template>

<script>
import { computed } from '@vue/runtime-core'
import { useStore } from 'vuex'
import Docker from '../../components/Docker.vue'
import ProductList from '../orderConfirm/ProductList.vue'

export default {
  name: 'Cart',
  components: { Docker, ProductList },
  setup () {
    const store = useStore()
    const { cartList } = store.state
    const isCartEmpty = computed(() => {
      let isEmpty = true
      for (const item in cartList) {
        console.log(item)
        if (Object.keys(cartList[item].productList).length > 0) {
          isEmpty = false
        }
      }
      return isEmpty
    })
    return { cartList, isCartEmpty }
  }
}
</script>

